/* Blog CSS */
#blog {
    padding: 6rem 0 4rem;

    .blog_wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;

        /* Desktop and Laptop */
        @media (max-width: 992px) {
            flex-direction: column-reverse;
        }

        .blog_col {
            width: calc(100% / 2 - 15px);
            height: auto;
            position: relative;
            z-index: 1;

            /* Desktop and Laptop */
            @media (max-width: 992px) {
                width: fit-content;
                padding: 2rem;
            }

            /* Mobile Phone */
            @media (max-width: 767px) {
                padding: 1.5rem;
            }
        }

        & h3 {
            width: 80%;
            font-size: 2.5rem;
            line-height: 3.714rem;
            font-weight: 600;
            text-transform: uppercase;
            margin-bottom: 20px;
          
            /* Desktop and Laptop */
            @media (max-width: 992px) {
                width: 100%;
                text-align: center;
            }

            /* Mobile Phone */
            @media (max-width: 767px) {
                width: 100%;
                font-size: 2.2rem;
                line-height: 3.5rem;
            }
        }

        & p {
            width: 80%;
            margin: 0;
            padding: 0;
            font-size: 1rem;
            line-height: 2rem;
            color: var(--font-color);

            /* Desktop and Laptop */
            @media (max-width: 992px) {
                width: 100%;
                text-align: center;
            }
        }

        .blog_image {
            position: relative;
            text-align: center;

            &:after {
                content: '';
                position: absolute;
                border: 10px solid var(--secondary-color);
                right: 45px;
                left: -45px;
                bottom: 45px;
                top: -45px;
                z-index: -1;
                border-radius: 0 2.1429rem;
            }

            /* Mobile Phone */
            @media (max-width: 767px) {
                &:after {
                    left: 0;
                }
            }

            & img {
                border-radius: 0 2.1429rem;
                max-width: 100%;

                /* Mobile Phone */
                @media (max-width: 767px) {
                    max-width: 85%;
                }
            }
        }
    }
}